@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*===== GOOGLE FONTS =====*/

/*===== VARIABLES CSS =====*/
:root{
  --header-height: 3rem;
  --nav-width: 324px;
  
  /*===== Font weight =====*/
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;

  /*===== Colors =====*/
  --first-color: #458d57;
  --first-color-dark: #662616;
  --first-color-darken: #290F09;
  --text-color: #54423d;
  --first-color-light: #EAE7E6;
  --first-color-lighten: #FFFAFA;

  /*===== Font and typography =====*/
  --body-font: 'Poppins', sans-serif;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*===== Margines =====*/
  --mb-1: .5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  
  /*===== z index =====*/
  --z-fixed: 100;
}

@media screen and (min-width: 768px){
  :root{
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/*===== BASE =====*/
*,::before,::after{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body{
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  font-weight: 500;
  font-weight: var(--font-medium);
  background-color: #FFFAFA;
  background-color: var(--first-color-lighten);
  color: #54423d;
  color: var(--text-color);
  line-height: 1.6;
}

h1,h2,h3,p{
  margin: 0;
}

h3{
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

a{
  text-decoration: none;
  color: #54423d;
  color: var(--text-color);
}

img{
  max-width: 100%;
  height: auto;
  display: block;
}

/*===== CLASS CSS ===== */
.section{
  padding: 4rem 0 2rem;
}

.section-title{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #458d57;
  color: var(--first-color);
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
  text-align: center;
}

.section-subtitle{
  display: block;
  font-size: .813rem;
  font-size: var(--small-font-size);
  color: #290F09;
  color: var(--first-color-darken);
  text-align: center;
  font-weight: 700;
  font-weight: var(--font-bold);
  margin-bottom: .25rem;
}

/*===== LAYOUT =====*/
.bd-grid{
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.app-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #FFFAFA;
  background-color: var(--first-color-lighten);
  box-shadow: 0 2px 4px rgba(0, 0, 0, 1);
}

/*===== NAV =====*/
.nav{
  height: 3rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__menu{
  position: fixed;
  top: 0;
  left: -100%;
  width: 50%;
  height: 100vh;
  padding: 3rem;
  display: flex;
  align-items: center;
  background-color: #458d57;
  background-color: var(--first-color);
  transition: .5s;
}

.nav__close{
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 2rem;
  color: #662616;
  color: var(--first-color-dark);
  cursor: pointer;
}

.nav__item{
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.nav__link{
  font-size: 1rem;
  text-transform: uppercase;
  color: #FFFAFA;
  color: var(--first-color-lighten);
  font-weight: 700;
  font-weight: var(--font-bold);
  transition: .3s;
}

.nav__link:hover{
  color: #54423d;
  color: var(--text-color);
}

.nav__toggle{
  color: #662616;
  color: var(--first-color-dark);
  font-size: 1.3rem;
  cursor: pointer;
}

/*=== Show menu ===*/
.show{
  left: 0;
}

/*Active menu*/
.active{
  color: #54423d;
  color: var(--text-color);
}

/*===== HOME =====*/
.home__container{
  height: calc(100vh - 3rem);
  height: calc(100vh - var(--header-height));
  align-items: center;
}

.home__data{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  text-align: center;
}

.home__img{
  padding-top: .1rem;
  padding-left: .5rem;
  width: 160px;
  height: 160px;
  background-color: #458d57;
  background-color: var(--first-color);
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.home__img img{
  width: 655px;
}

.home__title{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #458d57;
  color: var(--first-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

.home__profession{
  font-size: .813rem;
  font-size: var(--small-font-size);
  color: #662616;
  color: var(--first-color-dark);
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-5);
}

.home__social{
  margin-bottom: 5rem;
}

.home__social-link{
  display: inline-flex;
  background-color: #458d57;
  background-color: var(--first-color);
  color: #FFFAFA;
  color: var(--first-color-lighten);
  font-size: 1.1rem;
  border-radius: 50%;
  padding: .40rem;
  margin: 0 .5rem;
  margin: 0 var(--mb-1);
  transition: .3s;
}

.home__social-link:hover{
  background-color: #662616;
  background-color: var(--first-color-dark);
}

.home__button{
  width: 100%;
}


/*BUTTONS*/
.button{
  display: inline-block;
  background-color: #458d57;
  background-color: var(--first-color);
  color: #FFFAFA;
  color: var(--first-color-lighten);
  padding: 1rem 2rem;
  font-weight: 600;
  font-weight: var(--font-semi-bold);
  border-radius: .5rem;
  transition: .3s;
}

.button:hover{
  background-color: #662616;
  background-color: var(--first-color-dark);
}

.button__light{
  background-color: #FFFAFA;
  background-color: var(--first-color-lighten);
  color: #458d57;
  color: var(--first-color);
}

.button__light:hover{
  color: #FFFAFA;
  color: var(--first-color-lighten);
}

/* ===== ABOUT =====*/
.about__container{
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.about__data{
  text-align: center;
}

.about__description{
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.about__img{
  width: 150px;
  border-radius: .5rem;
  margin: 0 auto;
}

.about__information{
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.about__information-title{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #662616;
  color: var(--first-color-dark);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.about__information-data{
  display: flex;
  align-items: center;
  padding-left: 1.5rem;
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.about__information-icon{
  font-size: 1.5rem;
  color: #458d57;
  color: var(--first-color);
  margin-right: 2rem;
  margin-right: var(--mb-4);
}

.about__information-subtitle{
  display: block;
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

.about__information-subtitle-small{
  font-size: .813rem;
  font-size: var(--small-font-size);
}

/* ===== SKILLS =====*/
.skills__container{
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.skills__subtitle{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #662616;
  color: var(--first-color-dark);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.skills__data{
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0 2rem;
  margin: 0 var(--mb-4);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.skills__name{
  text-transform: uppercase;
}

.skills__bar{
  position: absolute;
  bottom: -.75rem;
  height: .25rem;
  background-color: #458d57;
  background-color: var(--first-color);
}

.skills__html{
  width: 99%;
}

.skills__js{
  width: 85%;
}

.skills__react{
  width: 90%;
}

.skills__angular{
  width: 75%;
}

.skills__py{
  width: 80%;
}

.skills__php{
  width: 70%;
}

.skills__node{
  width: 60%;
}

.skills__firebase{
  width: 50%;
}

/* ===== EDUCATION =====*/
.education__container{
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.education__content{
  display: grid;
  grid-template-columns: 1fr -webkit-max-content 1fr;
  grid-template-columns: 1fr max-content 1fr;
}

.education__year, .education__race{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  margin-bottom: .25rem;
}

.education__college, .education__major{
  display: block;
  font-size: .75rem;
  font-size: var(--smaller-font-size);
}

.education__year, .education__college{
  text-align: right;
}

.education__time{
  padding: 0 1rem;
  justify-self: center;
}

.education__rounder{
  position: relative;
  display: inline-block;
  width: 12px;
  height: 12px;
  background-color: #458d57;
  background-color: var(--first-color);
  border-radius: 50%;
}

.education__rounder::before{
  content: '';
  position: absolute;
  transform: translate(-4px, -4px);
  width: 20px;
  height: 20px;
  border: 1px solid #458d57;
  border: 1px solid var(--first-color);
  border-radius: 50%;
}

.education__line{
  display: block;
  height: 90%;
  width: 2px;
  background-color: #458d57;
  background-color: var(--first-color);
  transform: translate(5px, -4px);
}

/* ===== SERVICES =====*/
.services__container{
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.services__content{
  padding: 1.5rem .5rem;
  border: 2px solid #458d57;
  border: 2px solid var(--first-color);
  border-radius: .5rem;
  text-align: center;
}

.services__icon{
  font-size: 2rem;
  padding: .5rem;
  background-color: #458d57;
  background-color: var(--first-color);
  color: #FFFAFA;
  color: var(--first-color-lighten);
  border-radius: 50%;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.services__title{
  font-size: 1.25rem;
  color: #662616;
  color: var(--first-color-dark);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.services__content, .services__icon, .services__description, .services__title{
  transition: .5s;
}

.services__content:hover{
  background-color: #458d57;
  background-color: var(--first-color);
}

.services__content:hover .services__icon{
  background-color: #FFFAFA;
  background-color: var(--first-color-lighten);
  color: #458d57;
  color: var(--first-color);
}

.services__content:hover .services__title{
  color: #FFFAFA;
  color: var(--first-color-lighten);
}

.services__content:hover .services__description{
  color: #FFFAFA;
  color: var(--first-color-lighten);
}

/* ===== PROJECT IN MIND  =====*/
.project{
  background-color: #458d57;
  background-color: var(--first-color);
  padding-bottom: 0;
}

.project__container{
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.project__data{
  text-align: center;
}

.project__title, .project__description{
  color: #FFFAFA;
  color: var(--first-color-lighten);
}

.project__description{
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.project__img{
  padding-bottom: 6.5rem;
  width: 220px;
  justify-self: center;
}

/* ===== WORKS =====*/
.works__container{
  justify-items: center;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.works__img{
  position: relative;
  overflow: hidden;
  border-radius: .5rem;
}

.works__data{
  position: absolute;
  bottom: -100;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(204,75,44,.7);
  border-radius: .5rem;
  transition: .3s;
}

.works__link{
  display: inline-flex;
  font-size: 1.5rem;
  background-color: #FFFAFA;
  background-color: var(--first-color-lighten);
  color: #458d57;
  color: var(--first-color);
  padding: .25rem;
  border-radius: .25rem;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.works__title{
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #FFFAFA;
  color: var(--first-color-lighten);
}

.works__img:hover .works__data{
  bottom: 0;
}

/* ===== CONTACT =====*/
.contact__container{
  grid-row-gap: 3rem;
  row-gap: 3rem;
}

.contact__input{
  width: 100%;
  padding: 1rem;
  outline: none;
  border: none;
  background-color: #EAE7E6;
  background-color: var(--first-color-light);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  border-radius: .5rem;
}

.contact ::-webkit-input-placeholder{
  color: #662616;
  color: var(--first-color-dark);
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-weight: 500;
  font-weight: var(--font-medium);
}

.contact :-ms-input-placeholder{
  color: #662616;
  color: var(--first-color-dark);
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-weight: 500;
  font-weight: var(--font-medium);
}

.contact ::-ms-input-placeholder{
  color: #662616;
  color: var(--first-color-dark);
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-weight: 500;
  font-weight: var(--font-medium);
}

.contact ::placeholder{
  color: #662616;
  color: var(--first-color-dark);
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-weight: 500;
  font-weight: var(--font-medium);
}

.contact__button{
  outline: none;
  border: none;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  cursor: pointer;
}

.contact__info{
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.contact__subtitle{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #662616;
  color: var(--first-color-dark);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.contact__text{
  display: block;
  padding-left: 1rem;
}

/* ===== FOOTER =====*/
.footer{
  background-color: #290F09;
  background-color: var(--first-color-darken);
  color: #FFFAFA;
  color: var(--first-color-lighten);
  text-align: center;
}

.footer__title{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.footer__description{
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.footer__social{
  margin-bottom: 3rem;
}

.footer__link{
  display: inline-flex;
  background-color: #458d57;
  background-color: var(--first-color);
  color: #FFFAFA;
  color: var(--first-color-lighten);
  font-size: 1.1rem;
  border-radius: 50%;
  padding: .40rem;
  margin: 0 .5rem;
  margin: 0 var(--mb-1);
  transition: .3s;
}

.footer__link:hover{
  background-color: #FFFAFA;
  background-color: var(--first-color-lighten);
  color: #458d57;
  color: var(--first-color);
}

.footer__copy{
  font-size: .75rem;
  font-size: var(--smaller-font-size);
  color: #54423d;
  color: var(--text-color);
}

/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px){
  .home__button{
    width: auto;
    width: initial;
  }
  
  .nav__menu{
    width: 324px;
    width: var(--nav-width);
  }

  .about__container,
  .skills__container,
  .services__container,
  .works__container,
  .contact__container,
  .contact__inputs{
    grid-template-columns: repeat(2,1fr);
  }

  .about__data{
    text-align: left;
    text-align: initial;
  }

  .about__description{
    margin-bottom: 2.5rem;
    margin-bottom: var(--mb-5);
  }

  .about__img{
    width: 220px;
  }

  .about__information{
    padding-left: 4rem;
  }

  .education__time{
    padding: 0 2rem;
  }

  .project{
    background: none;
  }

  .project__container{
    background-color: #458d57;
    background-color: var(--first-color);
    grid-template-columns: 2fr 1.2fr;
    padding: 0 2rem;
    border-radius: .5rem;
  }

  .project__data{
    padding: 3rem 0;
  }

  .project__title, .project__data{
    text-align: left;
    text-align: initial;
  }

  .project__img{
    width: 230px;
    align-self: flex-end;
  }

  .contact__inputs{
    display: grid;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
  }

  textarea{
    resize: none;
  }

  .contact__info{
    padding-left: 3rem;
  }

  .footer{
    background: none;
  }

  .footer__container{
    background-color: #290F09;
    background-color: var(--first-color-darken);
    padding: 3rem 0;
    border-radius: .5rem;
  }
  
}

@media screen and (min-width: 1024px){

  body{
    margin: 0;
    padding-left: 324px;
    padding-left: var(--nav-width);
  }

  /*.1-header{
    width: 0;
  }*/

  .nav__toggle, .nav__logo, .nav__close{
    display: none;
  }

  .nav__menu{
    left: 0;
  }

  .home__container{
    height: 100vh;
  }

  .section{
    padding: 3rem 0 2rem;
  }

  .bd-grid{
    margin-left: auto;
    margin-right: auto;
  }

  .services__container,
  .works__container{
    grid-template-columns: repeat(3,1fr);
  }

}

